import React,{useState, useEffect} from 'react'
import { Items } from './Items'
import { Navbar } from './Navbar'
import {auth,fs} from '../config/config'
import {useNavigate} from 'react-router-dom'
import {Footer} from './Footer'


export const ShowProducts = () => {
    // getting usesr id and its name using firebase auth
    function GetUserUid(){
        const [uid, setUid]=useState(null);
        useEffect(()=>{
            auth.onAuthStateChanged(user=>{
                if(user){
                    setUid(user.uid);
                }
            })
        },[])
        return uid;
    }

    const uid = GetUserUid();

    // getting current user function
    function GetCurrentUser(){
        const [user, setUser]=useState(null);
        useEffect(()=>{
            auth.onAuthStateChanged(user=>{
                if(user){
                    fs.collection('users').doc(user.uid).get().then(snapshot=>{
                        setUser(snapshot.data().FullName);
                    })
                }
                else{
                    setUser(null);
                }
            })
        },[])
        return user;
    }


      // state of products
      const [myproducts, setMyProducts]=useState([]);

    //   // getting products function
    const getProducts = async ()=>{
          const myproducts = await fs.collection('Products').get();
          const productsArray = [];
          // getting its snapshort 
          for (var snap of myproducts.docs){
              var data = snap.data();
              data.ID = snap.id;
              productsArray.push({
                  ...data
              })
              if(productsArray.length === myproducts.docs.length){
                //setting the products
                  setMyProducts(productsArray);
              }
          }
      }
  
      useEffect(()=>{
          getProducts();
      },[])
  
      // state of totalProducts
      const [totalProducts, setTotalProducts]=useState(0);
      // getting cart products   
      useEffect(()=>{        
          auth.onAuthStateChanged(user=>{
              if(user){
                  fs.collection('Cart' + user.uid).onSnapshot(snapshot=>{
                      const qty = snapshot.docs.length;
                      setTotalProducts(qty);
                  })
              }
          })       
      },[])  
  
      // globl variable
      var Product;
  
      // add to cart
      const history = useNavigate();
      const addToCart = (myproducts)=>{
          if(uid!==null){
              // console.log(product);
              Product=myproducts;
              Product['qty']=1;
              Product['TotalProductPrice']=Product.qty*Product.price;
              fs.collection('Cart ' + uid).doc(myproducts.ID).set(Product).then(()=>{
                  console.log('successfully added to cart');
              })
  
          }
          else{
              history('/login');
          }
          
      }

    const user = GetCurrentUser();
 
    console.log(user);
    console.log(uid);
    // console.log(myproducts.le)
    return (
    <>
      
        <Navbar user={user} totalProducts={totalProducts}/>           
            <br></br>   
        {myproducts.length >0 && (
            <> 
            <div className='container'>
			<h3 className='text-center'>Here our top results that matches your search</h3>
		    </div>
            <div className='container-fluid'>
                <h1 className='text-center'></h1>
                <div className='products-box'>
                    <Items myproducts ={myproducts}  addToCart = {addToCart}/>
                </div>
            </div>
            </>
        )}

        {myproducts.length <1 && (

            <div className='container-fluid'>Please wait ...</div>
        )}
       
       <Footer/>
    </>
  )
}
